.u-dancing {
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-style: italic;
  color: #8ca3e4;
}

.u-center {
  text-align: center;
}

.animateToRight {
  animation: moveToRight 1s ease forwards;
}

.animateToLeft {
  animation: moveToLeft 1s ease forwards;
}
.animateUp {
  animation: moveUp 1s ease forwards;
}
